.border-radius-container {
  align-items: center;
  background: #21252b;
  color: #d7dae0;
  display: flex;
  flex-direction: column;
  gap: 32px;
  justify-content: center;
  min-height: 100vh;
  padding: 16px;
}

.border-radius-container__header {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.border-radius-container__header__logo {
  height: 40px;
}

.border-radius-container__header__title {
  display: inline-block;
  font-size: 40px;
  margin-left: 4px;
  text-align: center;
}

.border-radius-container__main {
  align-items: center;
  display: flex;
  gap: 32px;
  justify-content: center;
  max-width: 700px;
}

.border-radius-container__main__box {
  background: linear-gradient(45deg, #61afef, #73c97d);
  flex-grow: 1;
  height: 200px;
  width: 200px;
}

.border-radius-container__main__aside {
  flex-grow: 1;
}

.main__aside__controllers__input-block {
  display: flex;
  flex-direction: column;
}

.main__aside__controllers__input-block:not(:first-child) {
  margin-top: 16px;
}

.main__aside__controllers__input-block__label {
  font-weight: 500;
  margin-bottom: 4px;
}

.main__aside__controllers__input-block__input {
  appearance: none;
  -webkit-appearance: none;
  background: #282c34;
  border-radius: 8px;
  height: 8px;
  margin-top: 8px;
}

.main__aside__controllers__input-block__input::-webkit-slider-thumb {
  appearance: none;
  -webkit-appearance: none;
  background: linear-gradient(45deg, #61afef, #73c97d);
  border-radius: 8px;
  cursor: pointer;
  height: 24px;
  width: 24px;
}

.main__aside__controllers__input-block__input::-moz-range-thumb {
  appearance: none;
  -webkit-appearance: none;
  background: linear-gradient(45deg, #61afef, #73c97d);
  border-radius: 8px;
  cursor: pointer;
  height: 24px;
  width: 24px;
}

.border-radius-container__main__aside__code {
  background: #282c34;
  border-radius: 16px 16px 16px 16px;
  display: block;
  font-weight: 400;
  line-height: 24px;
  margin-top: 24px;
  padding: 16px;
  width: 400px;
}

.border-radius-container__main__aside__code__hr {
  background: #d7dae0;
  border: none;
  height: 1px;
  margin: 8px 0;
  width: 100%;
}

@media (max-width: 700px) {
  .border-radius-container__header__title {
    font-size: 32px;
  }

  .border-radius-container__main {
    flex-direction: column;
  }

  .border-radius-container__main__aside__code {
    width: 100%;
  }
}
